ï»¿html {
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-track {
    background-color: $color-sec;
  }
  &::-webkit-scrollbar-thumb {
    // border-radius: 10px;
    background-color: $color-prim;
  }

  overflow-y: hidden;

  scrollbar-width: thin;
  scrollbar-color: rgba($color-prim, 1) rgba($color-sec, 1);
}

body {
  font-family: "tj", sans-serif;
}

* {
  font-family: inherit;
}

input,
a,
button {
  font-family: "tj", sans-serif;

  &:focus {
    outline: none;
    text-decoration: none;
  }

  &:hover {
    text-decoration: none;
  }
}

button {
  cursor: pointer;
  border: 0;
  background-color: transparent;

  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
  }

  &:hover,
  &:active,
  &:focus {
    background-color: transparent;
    box-shadow: none;
  }

}

