@mixin Flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

@keyframes flash{
    0%{
        opacity: .5;
    }
    100%{
        opacity: 1;
    }
}

@keyframes spin{
    0%{
        transform: rotate(0deg)
    }
    100%{
        transform: rotate(360deg)
    }
}