// Preloader
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right bottom, #742e70, #9a5695);
  @include Flex;
  z-index: 99999;

  .spinner {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 0;
    border-left: 4px solid $color-white;
    animation: spin 1s linear infinite;
  }
}

.sk-chase {
  width: 80px;
  height: 80px;
  position: relative;
  animation: sk-chase 2.5s infinite linear both;
}

.sk-chase-dot {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0; 
  animation: sk-chase-dot 2.0s infinite ease-in-out both; 
}

.sk-chase-dot:before {
  content: '';
  display: block;
  width: 25%;
  height: 25%;
  background-color: #fff;
  border-radius: 100%;
  animation: sk-chase-dot-before 2.0s infinite ease-in-out both; 
}

.sk-chase-dot:nth-child(1) { animation-delay: -1.1s; }
.sk-chase-dot:nth-child(2) { animation-delay: -1.0s; }
.sk-chase-dot:nth-child(3) { animation-delay: -0.9s; }
.sk-chase-dot:nth-child(4) { animation-delay: -0.8s; }
.sk-chase-dot:nth-child(5) { animation-delay: -0.7s; }
.sk-chase-dot:nth-child(6) { animation-delay: -0.6s; }
.sk-chase-dot:nth-child(1):before { animation-delay: -1.1s; }
.sk-chase-dot:nth-child(2):before { animation-delay: -1.0s; }
.sk-chase-dot:nth-child(3):before { animation-delay: -0.9s; }
.sk-chase-dot:nth-child(4):before { animation-delay: -0.8s; }
.sk-chase-dot:nth-child(5):before { animation-delay: -0.7s; }
.sk-chase-dot:nth-child(6):before { animation-delay: -0.6s; }

@keyframes sk-chase {
  100% { transform: rotate(360deg); } 
}

@keyframes sk-chase-dot {
  80%, 100% { transform: rotate(360deg); } 
}

@keyframes sk-chase-dot-before {
  50% {
    transform: scale(0.4); 
  } 100%, 0% {
    transform: scale(1.0); 
  } 
}

// Preloader

// Mob Nav
.navover {
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100%;
  background-color: rgba(#33BBC7, .95);
  z-index: 999;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;

  span {
    display: none;
    color: #fff;
    font-size: 25px;
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
  }

  &.open {
    opacity: 1;
    visibility: visible;

    span {
      display: block;
    }
  }
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #32BAC6;
  z-index: 999;
  // overflow-y: auto;
  transition: 0.5s;
  @include Flex;
  flex-direction: column;
  transform-origin: right;
  transform: scaleX(0);

  &:after{
    content: "";
    position: absolute;
    width: 70%;
    height: 200%;
    background-color: #FFF;
    transform: rotate(45deg);
    z-index: -1;
  }

  html[dir="ltr"] & {
    transform-origin: left;
  }

  &.open {
    // transform-origin: left;
    transform: scaleX(1);

    ul {
      opacity: 1;

      li {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }

  &.origin {
    transform-origin: left;

    html[dir="ltr"] & {
      transform-origin: right;
    }
  }

  .options {
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: 50px;

    .lang a {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      color: #4b4c4c;
      width: 32px;
      height: 32px;
      background-color: transparent;

      html[dir="ltr"] & {
        color: #fff;
      }
    }

    .search_pop {
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: $color-sec;
      color: #fff;
      font-size: 14px;
      margin-left: auto;
      margin-right: 20px;

      html[dir="ltr"] & {
        margin-left: 20px;
        margin-right: auto;
      }
    }

    .close1 {
      display: flex;
      font-size: 18px;
      color: #777;
      cursor: pointer;

      span{
        color: #FFF;
        html[dir="ltr"] & {
          color: #333;
        }
      }
    }
  }

  & > ul {
    display: flex;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0.5s;

    & > li {
      position: relative;
      padding: 5px 0;
      // margin-bottom: 20px;
      transition: 0.5s;
      transform: translateX(10px);
      html[dir="ltr"] & {
        transform: translateX(-10px)
      }
      opacity: 0;

      &:nth-child(1) {
        transition-delay: 0.3s;
      }
      &:nth-child(2) {
        transition-delay: 0.5s;
      }
      &:nth-child(3) {
        transition-delay: 0.7s;
      }
      &:nth-child(4) {
        transition-delay: 0.9s;
      }
      &:nth-child(5) {
        transition-delay: 1.1s;
      }
      &:nth-child(6) {
        transition-delay: 1.3s;
      }

      &.drop {

        > a{
          position: relative;
          > span{
            margin-right: 10px;
            margin-left: -20px;
            html[dir="ltr"] & {
              margin-right: -20px;
              margin-left: 10px;
            }
            font-size: 10px;
            color: #333;
          }
        }

        &.open {
          span {
            transform: rotate(180deg);
          }
        }
      }

      a {
        width: 100%;
        display: block;
        font-family: "tjMd";
        position: relative;
        font-size: 17px;
        color: #333;
        text-align: center;
        transition: 0.5s;
      }

      .megaMenu {
        display: none;
        margin-top: 10px;

        li {
          &:not(:last-child) {
            margin-bottom: 6px;
          }

          a {
            font-size: 15px;
            color: #777;
          }
        }
      }

      .drop {
        width: 100%;
        padding: 5px 15px;
        display: none;
        overflow: visible;
        height: auto;

        li {
          padding: 4px 0;

          a {
            font-size: 15px;
            color: #898989;
            transition: 0.5s;

            &:hover {
              color: #595959;
            }
          }
        }
      }
    }
  }
}

