    .menuTriger, .searchTriger{
      display: none !important;

      @include Sm{
        display: flex !important;

        span{
          font-size: 18px;
          margin-left: 5px;
          html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 5px;
          }
        }
