        top: 70%;
        left: 50%;
        z-index: 999;
        background-color: transparent;
        width: 80%;
        margin: 0;
        height: auto;
        border: 0;
        transform: translateY(90px) translateX(-50%);
        opacity: 0;
        visibility: hidden;
