                display: flex;
                justify-content: center;
                margin: 0;
                padding: 6px 0;
                transition: 0.5s;

                &:hover {
                  color: #2bb7c4;
                }
              }
            }
          }
        }

        &:hover {
          .dropDown {
            transform: translateX(-50%) translateY(10px);
            opacity: 1;
            visibility: visible;
          }
        }
      }

      .wish {
        span {
          font-size: 18px;
          margin-left: 5px;
          html[dir="ltr"] & {
            margin-right: 5px;
            margin-left: 0;
          }
        }

