          height: 12px;
          background-color: #3abdcb;
          border-radius: 50%;
          font-size: 10px;
          color: #fff;
          font-family: "tjMd";
          @include Flex;
          position: absolute;
          top: -5px;
          right: -3px;
          html[dir="ltr"] & {
            right: unset;
            left: -3px;
          }
          line-height: 1;
        }
