// footer

footer {
  padding: 65px 0 70px;
  background-image: linear-gradient(to right, #742E70, #9A5695);

  @include Sm {
    padding: 40px 0;
  }

  .foot-links {
    // @include Lg {
    //   text-align: right;
    // }

    @include Sm {
      margin-bottom: 15px;
    }

    .collapse {
      display: block;

      @include Sm {
        display: none;
      }

      &.in {
        display: block;
      }
    }

    & > button {
      font-size: 16px;
      font-family: "tjBold";
      color: #fff;
      margin-bottom: 15px;
      background-color: transparent;
      border: 0;
      padding: 0;
      pointer-events: none;

      &.trans {
        &:after {
          content: "\2212";
        }
      }

      @include Sm {
        width: 100%;
        margin: auto;
        pointer-events: all;
        margin-bottom: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      @include mob480 {
        width: 100%;
      }

      &:after {
        content: "\002B";
        margin-left: 5px;
        font-size: 18px;
        float: left;
        display: none;

        @include Sm {
          display: block;
        }
      }
    }

    ul {
      @include Sm {
        padding-top: 10px;
        padding-right: 10px;
      }

      li {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
    }

    a {
      font-size: 14px;
      font-family: "tj";
      color: #ffd5fd;
      transition: 0.5s;

      &:hover {
        color: $color-white;
      }
    }
  }

  .newsletter {
    @include Md {
      margin-top: 35px;
      text-align: center;
    }

    @include Lg {
      margin-top: 0;
      text-align: right;
    }

    @include Sm {
      margin-top: 35px;
      text-align: center;
    }

    & > p {
      font-size: 16px;
      font-family: "tjBold";
      color: #fff;
      margin-bottom: 20px;
    }

    & > span {
      font-size: 14px;
      font-family: "tj";
      color: #fff;
      margin-bottom: 15px;
      display: block;
    }
  }

  .frm {
    position: relative;
    margin-bottom: 17px;
    width: 360px;

    @include Md {
      margin: auto;
      margin-bottom: 17px;
    }

    @include Sm {
      margin: auto;
      margin-bottom: 17px;
    }

    @include mob375 {
      width: 280px;
    }

    input {
      padding: 0 20px;
      height: 45px;
      width: 100%;
      border: 0;
      color: #bbbbbb;
      border-radius: 100px;
      font-size: 14px;
      background-color: #FFF;
      transition: 0.5s;

      &::placeholder {
        color: #bbbbbb;
      }
    }

    button {
      position: absolute;
      top: 50%;
      left: 6px;
      html[dir="ltr"] & {
        left: unset;
        right: 6px;
      }
      transform: translateY(-50%);
      width: 35px;
      height: 35px;
      border-radius: 50%;
      background-color: #2BB7C4;
      border: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: .5s;

      span{
        font-size: 14px;
        color: #FFF;
      }

      &:hover {
        background-color: #742E70;
      }
    }
  }

  .social {
    @include Flex;

    a {
      @include Flex;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: rgba($color-white, 0.1);
      font-size: 18px;
      transition: 0.5s;

      &:not(:last-child) {
        margin-left: 5px;

        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 5px;
        }
      }

      i{
        color: #FFF;
      }

      &:hover {
        background-color: #FFF;
        i{
          color: #7B3678;
        }
      }
    }
  }
}

