// End

.end {
  background-image: linear-gradient(to right, #742E70, #9A5695);

  .container {
    border-top: 1px solid #9A5F95;
    padding: 0;
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mob600 {
      flex-direction: column-reverse;
    }

    @include Xs {
      text-align: center;
    }

    p,
    a {
      font-size: 14px;
      color: #FFF;
      margin-bottom: 0;

      @include Xs {
        font-size: 13px;
      }

      @include mob600 {
        margin-bottom: 10px;
      }
    }

    p {
      @include mob480 {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-bottom: 0;
      }
    }

    a {
      img {
        width: 58px;
        height: 21px;
        backface-visibility: hidden;
        transform: translateZ(0);
        margin-right: 5px;
        html[dir="ltr"] & {
          margin-right: 0;
          margin-left: 5px;
        }
      }

      @include mob480 {
        margin-top: 10px;
      }
    }

    > img{
      width: 220px;
      height: 44px;
      backface-visibility: hidden;
      transform: translateZ(0);
    }
  }
